<template>
  <div id="show-blog" v-them:bgcolor>
    <h1>Blog List</h1>
    <input type="text" placeholder="search title" v-model="search"/>
    <div class="blog-list" v-for="blog in filterBlogs">
      <router-link :to="/detail/ + blog.id">
        <h3 v-raidrow="'red'">{{blog.id}}{{blog.title | to-upper}}</h3>
      </router-link>
      <p>{{blog.content | str-substring}}</p>
    </div>
  </div>
</template>

<script>
  export default {
    name: "ShowBlog",
    data() {
      return {
        blogs: [],
        search: ''
      }
    },
    created() {
      // this.$http.get("http://jsonplaceholder.typicode.com/posts")
      //   .then((data) => {
      //     this.blogs = data.body.slice(0, 4);
      //   })
      this.$http.get("https://vue-demo-d8bc1.firebaseio.com/blogs.json")
        .then((data) => {
          return data.json();
        })
        .then((data) => {
          var array = [];
          for(let key in data) {
            data[key].id = key;
            array.push(data[key]);
          }
          this.blogs = array;
        })
    },
    computed: {
      filterBlogs: function () {
        return this.blogs.filter((blog) => {
          return blog.title.match(this.search);
        });
      }
    },
    filters: {
      toUpper: function (value) {
        return value.toUpperCase();
      },
      strSubstring(value) {
        return value.substring(0, 100) + "...";
      }
    },
    directives: {
      raidrow: {
        bind(el, binding, vnode) {
          if(binding.value == "red") {
            el.style.color = "#" + Math.random().toString(16).substring(2,8);
          } else {
            el.style.color = "blue";
          }
        }
      },
      them: {
        bind(el, binding, vnode) {
          if(binding.arg == 'bgcolor') {
            el.style.backgroundColor = "gray";
            el.style.padding = "20px";
          }
        }
      }
    }
  }
</script>

<style scoped>
  #show-blog {
    max-width: 800px;
    margin: 0 auto;
  }
  h1 {
    text-align: center;
  }
  .blog-list {
    padding: 20px;
    margin: 20px 0;
    box-sizing: border-box;
    background: #eee;
  }
  input {
    width: 100%;
    height: 30px;
  }
</style>



