<template>
  <div class="detail-blog">
    <h3>{{blog.id}} || {{blog.title}}</h3>
    <p>content: {{blog.content}}</p>
    <p>author: {{blog.author}}</p>
    <p>
      categories:
    </p>
    <ul>
      <li v-for="cat in blog.categories">{{cat}}</li>
    </ul>
  </div>
</template>

<script>
  export default {
    name: "DetailBlog",
    data() {
      return {
        blog: {}
      }
    },
    created() {
      this.$http.get("https://vue-demo-d8bc1.firebaseio.com/blogs/"+this.$route.params.id + ".json")
        .then((data) => {
          this.blog = data.body;
          this.blog.id = this.$route.params.id;
        })
    }
  }
</script>

<style scoped>
  .detail-blog {
    max-width: 800px;
    margin: 0 auto;
    padding: 20px;
    background: gray;
    border: 1px solid #aaa;
  }
</style>



