<template>
  <div id="add-blog">
    <h2>Add Blog Page</h2>
    <form>
      <label>Title</label>
      <input type="text" v-model="blog.title" />

      <label>Content</label>
      <textarea v-model="blog.content"></textarea>

      <label>Category</label>
      <div id="checkboxes">
        <label>Vue.js</label>
        <input type="checkbox" v-model="blog.categories" value="Vue.js" />
        <label>Angular4</label>
        <input type="checkbox" v-model="blog.categories" value="Angular4" />
        <label>Node.js</label>
        <input type="checkbox" v-model="blog.categories" value="Node.js" />
        <label>React.js</label>
        <input type="checkbox" v-model="blog.categories" value="React.js" />
      </div>

      <label>Author</label>
      <select v-model="blog.author">
        <option v-for="author in authors">{{author}}</option>
      </select>

      <button @click.prevent="addBlog">AddBlog</button>
    </form>
  </div>
</template>

<script>
  export default {
    name: "AddBlog",
    data() {
      return {
        blog: {
          title: "",
          content: "",
          categories: [],
          author: ""
        },
        authors: ["Tom", "Jack", "Tony"]
      }
    },
    methods: {
      addBlog() {
        this.$http.post("https://vue-demo-d8bc1.firebaseio.com/blogs.json", this.blog)
          .then((data) => {
            console.log(data)
          })
      }
    }
  }
</script>

<style scoped>
  #add-blog * {
    box-sizing: border-box;
  }
  #add-blog {
    margin: 20px auto;
    max-width: 600px;
    padding: 20px;
  }
  h2 {
    text-align: center;
  }
  label {
    display: block;
    margin: 20px 0 10px;
  }
  input[type="text"], textarea, select {
    display: block;
    width: 100%;
    padding: 8px;
  }
  textarea {
    height: 200px;
  }
  #checkboxes label {
    display: inline-block;
    margin-top: 0;
  }
  #checkboxes input {
    display: inline-block;
    margin-right: 20px;
  }
  button {
    display: block;
    margin: 20px 0;
    background: red;
    color: #fff;
    border: 0;
    border-radius: 5px;
    padding: 15px 20px;
    font-weight: bold;
    cursor: pointer;
  }
</style>



